import React from 'react';
import './Footer.css';
import Footer from './Footer.js';

// Privacy Policy Page
function PrivacyPolicy(props) {
    return (
        <div className='PrivacyPolicy'>
            <div className='PrivacyPolicyTextContainer'>
                <h1>Privacy Policy</h1>
                <p>Home Together Canada is an online platform that provides individuals, wishing to share a home with others, the ability to find each other and the ability to find the sharing services and shared homes they want or need in their endeavor to succeed at creating a shared living experience of their own making.</p>
            </div>
            <div className='PrivacyPolicyTextContainer'>
                <h2>1. Definition of Personal Information.</h2>
                <p>Personal information is defined as information that can identify an individual and information about an identifiable individual, which means a person can be identified by the information either directly or in combination with other information.</p>
            </div>
            <div className='PrivacyPolicyTextContainer'>
                <h2>2. Collection of Personal Information.</h2>
                <p>We collect your personal information as a way to ensure you are who you say you are, (a) to provide basic information on the number of individuals in different demographic groups in different areas at any given point in time.  (b) to provide you the ability to search for others in the demographic grouping of your choice on the website. We collect personal information from you when you register for an Account; provide us information on a web form; update or add information to your Account; or when you otherwise correspond with us.
                Personal information that we may collect may include:
                identifying information such as your name, addresses, telephone number, email addresses, and IP addresses;
                location data and demographic data such as your gender and age;
                any content that you generate, or that is connected to you, as a result of creating an Account
                we collect your information in order to authenticate or identify you or to verify the information we have collected.
                
                We provide you with information, links and classified advertisements on hometogether.ca. We do not share your personal information with these third parties.  When you access these third-parties, they may collect personal information from you. Please note that this Privacy Policy covers the collection of personal information by us only and does not cover the collection of personal information by any third-party ad companies.</p>
            </div>
            <div className='PrivacyPolicyTextContainer'>
                <h2>3. Use of Personal Information.</h2>
                <p>We use the personal information we collect to: Provide, improve, and personalize our content and the Services, communicate with you when necessary. Detect, prevent, mitigate, and investigate fraudulent or illegal activities; and enforce our Terms of Service and this Privacy Policy.</p>
            </div>
            <div className='PrivacyPolicyTextContainer'>
                <h2>4. Security and Retention of Personal Information.</h2>
                <p>We retain your personal information as long as it is necessary and relevant for our operations. In addition, we may retain your personal information, including information from closed Accounts, to comply with applicable laws, prevent fraud, collect any fees owed, resolve disputes, troubleshoot problems, assist with any investigation, enforce our Terms of Service, or take other actions permitted or required by applicable laws. After it is no longer necessary for us to retain your personal information, as determined by us in our sole discretion in accordance with applicable laws, we dispose of it in a secure manner according to our data retention and deletion policies.
                We use a payment service and therefore do not ask for or store your financial information. All moneys coming to us, come directly from the payment provider.
                In recognition of our legal obligations to protect your personal information, we endeavor to keep our website updated.</p>
            </div>
            <div className='PrivacyPolicyTextContainer'>
                <h2>5. Location of Servers.</h2>
                <p>We create, maintain, and archive information on servers that are not located on our immediate premises and are located in Canada or the USA.</p>
            </div>
            <div className='PrivacyPolicyTextContainer'>
                <h2>6. Minors.</h2>
                <p>The Website, Application, and Services are intended for general audiences and are not directed to individuals under 19. We do not knowingly collect personal information from Minors. If you are a Minor, please do not use the Website, Application, or Services or submit any personal information through the Website or Application. If you have reason to believe that a Minor has provided their personal information to us, please contact us at the email address provided below, and we will delete that information.</p>
            </div>
            <div className='PrivacyPolicyTextContainer'>
                <h2>7. Disclosure of Personal Information.</h2>
                <p>We do not disclose your personal information to third parties. There are, however, certain circumstances in which we may share your personal information with certain third parties without further notice to you. This disclosure may be required to comply with our legal obligations, to enforce our Terms of Service, or to prevent, detect, mitigate, and investigate fraudulent or illegal activities related to our Services. These circumstances include, but are not limited to:</p>
                <h3>Law Enforcement</h3>
                <p>We cooperate with government and law enforcement officials or private parties to enforce and comply with applicable laws. We may disclose any information about you to government or law enforcement officials or private parties as we, in our sole discretion, believe necessary or appropriate to respond to claims, legal process (including subpoenas), to protect our property or rights or the property or rights of a third party, the safety of the public or any person, to prevent or stop any illegal, unethical, or legally actionable activity, or to otherwise comply with applicable laws.</p>
                <h3>Service Providers</h3>
                <p>We engage certain trusted third parties to perform functions and provide services to us, including, without limitation, hosting and maintenance, database storage and management. We will share your personal information with these third parties, but only to the extent necessary to perform these functions and provide such services, and only pursuant to binding contractual obligations requiring such third parties to maintain the privacy and security of your data.</p>
            </div>
            <div className='PrivacyPolicyTextContainer'>
                <h2>8. Consent for Collection, User, and Disclosure.</h2>
                <p>By accessing and using our Website, we consider you to have given us consent to the collection, use, and disclosure of your personal information in accordance with this Privacy Policy. Once this consent has been given, we will continue to collect, use, and disclose your personal information for purposes set out in this Privacy Policy without obtaining further written or verbal consent to do so. </p>
            </div>
            <div className='PrivacyPolicyTextContainer'>
                <h2>9. Cookies</h2>
                <p>We do not use cookies and this site was not created to use cookies. Note that this Privacy Policy covers the use of cookies by us only and does not cover the use of cookies by any third-party ad companies.</p>
            </div>
            <div className='PrivacyPolicyTextContainer'>
                <h2>10. Third Party Services</h2>
                <p>Our Website includes links to other websites. We provide these links and information to you as a service, much like a yellow pages. If you choose to register for such products or services, you are responsible for what information you choose to give them. We are not responsible for the privacy practices and or policies of such third parties and, therefore, you should review the privacy practices and policies of such third parties prior to providing your personal information in connection with such products or services.</p>
            </div>
            <div className='PrivacyPolicyTextContainer'>
                <h2>11. Withdraw Consent</h2>
                <p>You can choose not to provide any personal information to us or to withdraw your consent for the collection, use, or disclosure of your personal information for purposes set out in this Privacy Policy. If you choose to do so, you will not have access to the membership portions of the Website or Application </p>
            </div>
            <div className='PrivacyPolicyTextContainer'>
                <h2>12. Request to Access, Correct, or Delete</h2>
                <p>We take steps to ensure that the personal information we collect is accurate and up to date, and that you have the ability to access and make corrections to it. You can view and change your personal information by signing in to your Account. Please update your personal information immediately if it changes or is inaccurate.
Upon your request, we will close your Account.  Note however, that we attempt to retain registrant complaints as a way to monitor and in some cases stop individuals or businesses from re-registering to the site. </p>
            </div>
            <div className='PrivacyPolicyTextContainer'>
                <h2>13. Improper Collection and Misuse of Personal Information</h2>
                <p>If at any time you think your personal information has been accessed, please contact us immediately.</p>
            </div>
            <div className='PrivacyPolicyTextContainer'>
                <h2>14. Contact</h2>
                <p>If you have any questions or concerns about our Privacy Policy, or how we have handled your personal information please email us. You will find our email under contacts.</p>
            </div>
        <Footer Footer={Footer}></Footer>

        </div>

    );
}

export default PrivacyPolicy;
